import React, { Component } from 'react'
import {NavLink} from 'react-router-dom';

import '../../App.css'
 class Register extends Component {
    render() {
        return (
            <div className="container">
                <section>
                    <div className="row">
                        <div className="col s12">
                            <h4>Register</h4>
                        </div>
                    </div>
                </section>
                <div className="row">
                    <div className="col s12">
                        <NavLink to='/user'><div className="btn-large wave-effect">User</div></NavLink>
                    </div>
                </div> 
                <div className="row">
                    <div className="col s12">
                        <NavLink to='/resturant'><div className="btn-large wave-effect">Resturant</div></NavLink>
                    </div>
                </div> 
            </div>
        )
    }
}


export default Register