import React, { Component } from 'react'
import FoodItem from '../cat/Fooditem'
import {connect} from 'react-redux'
import {Link} from 'react-router-dom' 

class Bbq extends Component {
    render() {
        console.log(this.props.items.items.menuItem)
        const menuitems = this.props.items.items.menuItem
        
        return (
            <Link to="/details">
            <div>
                {menuitems.map((item)=>{
                    return <FoodItem header={item.title} key={item.id} content={item.content}/>
                })}
            </div>
            </Link>
        )
    }
}


const mapStateToProps = (state) => {
    return{
        items: state
    }       
}

export default connect(mapStateToProps)(Bbq)