import React, { Component } from 'react'

export default class MyRequest extends Component {
    render() {
        return (
            <div className="container">
                <section>
                <table className="striped">
                    <thead>
                        <tr>
                            <th>Status</th>
                            <th>Item Name</th>
                            <th>Item Price</th>
                        </tr>
                    </thead>

                    <tbody>
                        <tr>
                            <td>Pending</td>
                            <td>Eclair</td>
                            <td>$0.87</td>
                        </tr>
                    </tbody>
                </table>
                </section>
            </div>
        )
    }
}
