import React, { Component } from 'react'
import { Link } from "react-router-dom";
import {NavLink} from 'react-router-dom';
 class Navbar extends Component {
    render() {
        return (
                <nav className="">
                    <div className="nav-wrapper green">
                        <Link to="/" className="brand-logo">PC Hotel</Link>
                        <ul id="mobile-nav" className="right hide-on-med-and-down">
                           <li><NavLink to='/register'>Register</NavLink></li>
                           <li><NavLink to='/resturants'>Resturants</NavLink></li>
                           <li><NavLink to='/myrequest'>My Request</NavLink></li>
                           <li><NavLink to='/adminresturant'>Admin</NavLink></li>
                           </ul>
                    </div>
                </nav>
            )
    }
}


export default Navbar
