import React, { Component } from 'react'

 class ResturantReg extends Component {
    render() {
        return (
            <div className="container">
            <h5>Resturant Registration</h5>

            <div className='row'>
                <div className="col s12">
                    <div className="row">
                        <div className="col s5">
                            <div className="input-field">
                            <i className="material-icons prefix">account_circle</i>
                                <input type="text" id="firstName"></input>
                                <label htmlFor="first_name">First Name</label>
                            </div>
                        </div>
                        <div className="col s5 offset-l2">
                            <div className="input-field">
                                <input type="text" id="lastName"></input>
                                <label htmlFor="LastName">Last Name</label>
                            </div>            
                        </div>
                    </div>
                    <div className="row">
                            <div className="col s12">
                                <div className="input-field">
                                    <i className="material-icons prefix">email</i>
                                        <input type="email" id="email"></input>
                                        <label htmlFor="email" >Email</label>
                                </div>
                            </div>
                    </div>
                    
                    <div className="row">
                        <div className="col s6">
                            <div className="input-field">
                                <input type="text"  id="country"></input>
                                <label htmlFor="gender">Country</label>
                            </div>
                        </div>
                        <div className="col s6">
                            <div className="input-field">
                                <input type="text" id="city"></input>
                                <label htmlFor="City">City</label>
                            </div>
                        </div>
                    </div>

                    <div className="row">
                        <div className="col s12">
                            <div className="input-field">
                                
                                <input type="password" id="password"></input>
                                <label htmlFor="password">Password</label>
                            </div>
                        </div>
                    </div>
                    <div className="row">
                        <div className="col s12">
                            <div className="input-field">
                                <input type="password" id="confirmPassword"></input>
                                <label htmlFor="password">confirm password</label>
                            </div>
                        </div>
                    </div>
                    <div className="row">
                        <div className="col s12">
                            <div className="input-field">
                                <input type="text" id="resName"></input>
                                <label htmlFor="resName">Resturant Name</label>
                            </div>
                        </div>
                    </div>
                </div>

                <div className="row">
                    <div className="s12">
                        <div className="input-field">
                            <div className="btn waves-effect waves-light">
                                Submit
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        )
    }
}

export default ResturantReg;